<template>
  <div ref="container" style="width:100vw; height:calc(100vh - 64px);"></div>
</template>

<script>
  import { defineComponent, onMounted, onUnmounted, ref } from "vue";
  import * as THREE from "three";
  import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls';
  import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader'
  import { VRM, VRMSchema } from '@pixiv/three-vrm';

  export default defineComponent({
    setup() {
      // DOM
      const container = ref();
      // Three.js
      const renderer = new THREE.WebGLRenderer();
      const scene = new THREE.Scene();
      const camera = new THREE.PerspectiveCamera();

      let controls, axesHelper;
      const loader = new GLTFLoader();
      let vrm;

      // 
      const init = () => {
        if (container.value instanceof HTMLElement) {
          // DOM
          // renderer
          // 
          calculateWindowWidth()

          // camera.position.set( -1.0, 1.4, -2.0 );
          camera.position.set( -0.2, 1.4, -0.2 );
          // camera.lookAt(new THREE.Vector3(0, 1.6, 0));

          // light
          const light = new THREE.DirectionalLight( 0xffffff );
          light.position.set( 1.0, 1.0, 1.0 ).normalize();
          scene.add( light );

          // 
          // const controls = new OrbitControls(camera, renderer.domElement)
          controls = new OrbitControls(camera, renderer.domElement);
          controls.target.set(0, 0.85, 0);
          controls.screenSpacePanning = true;
          controls.update();

          // 
          scene.add(new THREE.GridHelper());

          // 
          axesHelper = new THREE.AxesHelper(0.5);
          scene.add(axesHelper);
          scene.remove(axesHelper);

          loader.load(
            // URL of the VRM you want to load
            '/models/three-vrm-girl.vrm',

            // called when the resource is loaded
            ( gltf ) => {
              // generate a VRM instance from gltf
              VRM.from( gltf ).then( ( _vrm ) => {
                // add the loaded vrm to the scene
                vrm = _vrm;
                container.value.appendChild(renderer.domElement);
      
                scene.add( vrm.scene );
                vrm.humanoid.getBoneNode(VRMSchema.HumanoidBoneName.RightUpperArm).rotation.z = -1;
                vrm.humanoid.getBoneNode(VRMSchema.HumanoidBoneName.RightLowerArm).rotation.y = 0.5;

                vrm.humanoid.getBoneNode(VRMSchema.HumanoidBoneName.LeftUpperArm).rotation.z = 1;
                vrm.humanoid.getBoneNode(VRMSchema.HumanoidBoneName.LeftLowerArm).rotation.y = -0.5;

                camera.position.set( -1 * 0.969, 0.723, -1.04 );
                camera.lookAt(new THREE.Vector3(0, 1.4, 0));
                // camera.rotateX(27.643);
                // camera.rotateY(-42.755);
                // camera.rotateZ(1.346);
                

                animate();
                
              } );
            },
            // called while loading is progressing
            ( progress ) => {
              console.log( 'Loading model...', 100.0 * ( progress.loaded / progress.total ), '%' )
            },
            // called when loading has errors
            ( error ) => console.error( error )
          );
          // 
        }
      };

      const blinkValue  = () => {
        return (Math.sin(performance.now()/700 * 1/3) ** 1024) +
          (Math.sin(performance.now()/700 * 4/7) ** 1024)
      }

      // 
      const animate = () => {
        const frame = () => {
          if(vrm) {
            // vrm1.position.set(0,0,1); 
            vrm.blendShapeProxy.setValue(
              VRMSchema.BlendShapePresetName.Blink,
              blinkValue()
            );
            // vrm1.blendShapeProxy.setValue(VRMSchema.BlendShapePresetName.Fun, 1.0);
            // vrm.humanoid.getBoneNode(VRMSchema.HumanoidBoneName.Chest).rotation.y = Math.sin(performance.now()/2000);

            // vrm.humanoid.getBoneNode(VRMSchema.HumanoidBoneName.Head).rotation.y = Math.sin(performance.now()/2000);
            // vrm.humanoid.getBoneNode(VRMSchema.HumanoidBoneName.Neck).rotation.y = Math.sin(performance.now()/2000);

            // vrm.humanoid.getBoneNode(VRMSchema.HumanoidBoneName.LeftLowerLeg).rotation.y = Math.sin(performance.now()/2000);
            // vrm.humanoid.getBoneNode(VRMSchema.HumanoidBoneName.LeftUpperLeg).rotation.y = Math.sin(performance.now()/2000);
            // vrm.humanoid.getBoneNode(VRMSchema.HumanoidBoneName.LeftFoot).rotation.y = Math.sin(performance.now()/2000);

            // vrm.humanoid.getBoneNode(VRMSchema.HumanoidBoneName.RightLowerLeg).rotation.y = Math.sin(performance.now()/2000);
            // vrm.humanoid.getBoneNode(VRMSchema.HumanoidBoneName.RightUpperLeg).rotation.y = Math.sin(performance.now()/2000);
            // vrm.humanoid.getBoneNode(VRMSchema.HumanoidBoneName.RightFoot).rotation.y = Math.sin(performance.now()/2000);

            // vrm.humanoid.getBoneNode(VRMSchema.HumanoidBoneName.LeftUpperArm).rotation.y = Math.sin(performance.now()/2000);
            // vrm.humanoid.getBoneNode(VRMSchema.HumanoidBoneName.LeftLowerArm).rotation.y = Math.sin(performance.now()/2000);
            // vrm.humanoid.getBoneNode(VRMSchema.HumanoidBoneName.LeftHand).rotation.y = Math.sin(performance.now()/2000);

            // vrm.humanoid.getBoneNode(VRMSchema.HumanoidBoneName.RightUpperArm).rotation.y = Math.sin(performance.now()/2000);
            // vrm.humanoid.getBoneNode(VRMSchema.HumanoidBoneName.RightLowerArm).rotation.y = Math.sin(performance.now()/2000);
            // vrm.humanoid.getBoneNode(VRMSchema.HumanoidBoneName.RightHand).rotation.y = Math.sin(performance.now()/2000);

            vrm.blendShapeProxy.update();
          }

          // 
          renderer.render(scene, camera);
          // 
          requestAnimationFrame(frame);
        };
        frame();
      };

      const calculateWindowWidth = () => {
        // 
        const { clientWidth, clientHeight } = container.value;
        console.log(clientWidth, clientHeight);

        // 
        renderer.setSize(clientWidth, clientHeight);
        renderer.setPixelRatio(clientWidth / clientHeight);

        // 
        camera.aspect = clientWidth / clientHeight;
        camera.updateProjectionMatrix(); 
      }

      // 
      onMounted(() => {
        init();
        window.addEventListener('resize', calculateWindowWidth, false);
      });

      onUnmounted(() => {
        window.removeEventListener('resize', calculateWindowWidth, false);
      });

      return {
        container,
      };
    },
  });

</script>